@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,600;0,900;1,100;1,400&display=swap);

.slide img{
    aspect-ratio: 16 / 9;
    max-height: 90vh;
    max-width: 90vw;
}

h3{
    font-family: 'Raleway', sans-serif;
    font-size: 3rem;
    text-align: center;
}

.data{
    position: absolute;
    top: 40%;
    left: 20%;
}

p{
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(----crema3);
    margin-top: 10px;
}

span{
    font-size: 1.3rem;
    display: flex;
    justify-content: center;
    margin: 10px;
}

.fa{
    font-size: 1.5rem;
    margin-right: 10px;
}
.sidebar-container{
    background-color: var(--fondo2);
    width: 22vw;
}

.perfil{
    /* background-color: aqua; */
    height: 32vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.avatar{
    /* background-color: brown; */
    width: 100px;
    height: 100px;
    border: 4px solid var(--fondo1);
    border-radius: 50%;
    background-image: url(/static/media/perfil.5a623d1f.jpeg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.enlaces{
    /* background-color: blueviolet; */
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.enlace{
    border: 1px solid #fff;
    padding: 10px;
    width: 100px;
    text-align: center;
    cursor: pointer;
}

.enlace:hover{
    background-color: #fff;
    color: #000;
}

.copy{
    /* background-color: chartreuse; */
    height: 8vh;
    font-size: .7rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.textyping-container{
    color: palevioletred;
    font-size: 1.2rem;
    white-space: nowrap;
    border-right: 3px solid;
    width: 10ch; /* tiene que ser el número de caracteres que tienen la línea */

    -webkit-animation: typing 1.8s steps(12) infinite alternate, blink .5s infinite alternate;

            animation: typing 1.8s steps(12) infinite alternate, blink .5s infinite alternate;
    overflow: hidden;
}

@-webkit-keyframes typing{
    from{
        width: 0
    }
}

@keyframes typing{
    from{
        width: 0
    }
}

@-webkit-keyframes blink{
    50% {
        border-color: transparent
    }
}

@keyframes blink{
    50% {
        border-color: transparent
    }
}
.home-container{
    display: flex;
}

.main{
    font-size: 2rem;
    background-image: url(/static/media/home-bg.1e0f9bf8.jpeg);
    background-color: #101010;
    /* filter:opacity(95%); */
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    vertical-align: middle;
    height: 100vh;
    width: 78vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main h1{
    font-family: 'Raleway', sans-serif;
    font-size: 4rem;
    text-align: center;
}

.buttons{
    display: flex;
    justify-content: center;
    margin: 10px 0px;
    flex-direction: column;
}

.button{
    border-radius: 30px;
    background-color: var(--ppal2);
    padding: 10px 20px;
    margin: 20px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    cursor: pointer;
}

.btn-icon{
    margin-right: 10px;
    font-size: 1rem;
}

@media screen and (min-width: 414px) {
    .buttons{
        flex-direction: row;
    }
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Poppins', sans-serif;
    font-size: 62.5%;
    background-color: #100F0F;
    background-color: var(--fondo2);
    color: #FFF;
    color: var(--fuente);
}

:root{
    --fuente: #FFF;
    /* Paleta 1 */
    --fondo1: #2C3333;
    --ppal1:  #395B64;
    --secundario1: #A5C9CA;
    --crema1: #E7F6F2;
    
    /* Paleta 2 */
    --fondo2: #100F0F;
    --ppal2: #0F3D3E;
    --secundario2: #E2DCC8;
    --crema2: #F1F1F1;

    /* Paleta 3 */
    --fondo3: #1B2430;
    --ppal3: #51557E;
    --secundario3: #816797;
    --crema3: #D6D5A8;
}
